<template>
    <div class="back-to-cart" v-loading="loading">
        <iframe :src="url" class="windows" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "backToCart",
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type:Object,
                default: {}
            }
        },
        created(){
            if(this.$isMobile()){
                this.$router.push({name:"distributorAudit"});
            }
        },
        computed: {
            url: {
                get() {

                    let pidArr = this.data.hasOwnProperty('pidArr') ? this.data.pidArr : "";
                    let idspecprice = this.data.hasOwnProperty('idspecprice') ? this.data.idspecprice : "";
                    let ismobile  = this.data.hasOwnProperty('ismobile') ? this.data.ismobile : "";
                    return this.$C('serverUrlAjax') + "/exusers/u_cart.php?&idweb=" + this.$getWebId() +"&ismobile="+ ismobile + "&act=add&ispc=1&lang=" + this.$lang() + "&v=9"+pidArr+idspecprice;
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .back-to-cart {
        position: relative;
        width: 100%;
        padding: 20px;

        .windows {
            position: relative;
            width: 100%;
            height: 500px;
        }
    }
</style>
<style>
    @media screen and (max-width:767px) {
        .back-to-cart{
            padding: 0px !important;
            height: 100% !important;
        }
        .windows{
            min-height: 600px !important;
        }
    }
    @media screen and (max-width:414px) {
        .windows{
            min-height: 660px !important;
        }
    }
    @media screen and (max-width:375px) {
        .windows{
            min-height: 600px !important;
        }
    }
    @media screen and (max-width:320px) {
        .windows{
            min-height: 510px !important;
        }
    }
</style>