<template>
    <div class="distributor-audit" v-if="tableData">
        <div class="content-view" v-if="type == 3">
            <div class="header">
                <div class="icon"><i class="el-icon-warning mainFontColor"></i></div>
                <div class="tips">
                    <p class="title">{{ $L('分销审核中') }}</p>
                    <p>{{ $L('还差一步哦，您需要在本店') }}
                        <span v-if="tableData.condition && tableData.condition.hasOwnProperty('dealAmountUp') && tableData.condition.dealAmountUp > 0">
                            {{ $L('消费满') }}{{ tableData.condition.dealAmountUp }}{{ $L('或') }}
                        </span>
                        <br>
                        <span v-if="tableData.condition && tableData.condition.hasOwnProperty('dealNumberUp') && tableData.condition.dealNumberUp > 0">
                            {{ $L('购买满') }}{{ tableData.condition.dealNumberUp }}{{ $L('笔订单,') }}
                        </span>
                        <br>
                        <span  v-if="tableData.condition && tableData.condition.hasOwnProperty('dealWith') && tableData.condition.dealWith == 0">
                            {{ $L('或') }}
                        </span>
                        <span  v-if="tableData.condition && tableData.condition.hasOwnProperty('dealWith') && tableData.condition.dealWith == 1">
                            {{ $L('且') }}
                        </span>
                        <span  v-if="tableData.condition && tableData.condition.hasOwnProperty('buyNum') && tableData.condition.buyNum == 999">
                            {{ $L('购买全部产品，即可自动成为分销商') }}
                        </span>
                        <span  v-if="tableData.condition && tableData.condition.hasOwnProperty('buyNum') && tableData.condition.buyNum == 1">
                            {{ $L('或购买指定任意一件产品，即可自动成为分销商') }}
                        </span>
                    </p>
                </div>
            </div>
            <div class="content-item"v-if="tableData && tableData.hasOwnProperty('prodList')">
                <p class="tips">{{ $L('指定产品') }}</p>
                <div class="left">
                    <ul class="content-ul">
                        <li class="content-list" v-for="item in tableData.prodList">

                            <div class="item">
                                <div class="head left-item">
                                    <el-checkbox-group v-model="checkedProdList" class="checkbox">
                                        <el-checkbox  :label="item" @change="handleCheckAllChange(item)">{{  }}</el-checkbox>
                                    </el-checkbox-group>
                                    <img :src="item.img" class="img">
                                </div>
                                <div class="right-item">
                                    <p class="name mainFontColor">{{ item.chrName }}</p>
                                    <p class="details">
                                        <span v-html="item.curSpec"></span>
                                        <span>{{ item.price }}</span>
                                    </p>
                                </div>

                            </div>
                        </li>
                    </ul>
                </div>

                <div class="right">
                    <el-button class="btn" type="primary" @click="goToBuy">{{ $L('立即购买') }}</el-button>
                    <el-button class="btn" @click="jumpFunc">{{ $L('去逛逛') }}</el-button>
                </div>
            </div>
        </div>
        <div class="content-view"  v-if="type == 2">
            <div class="header">
                <div class="icon"><i class="el-icon-warning mainFontColor"></i></div>
                <div class="tips">
                    <p class="title">{{ $L('分销审核中') }}</p>
                    <p>{{ $L('您的申请我们已经收到，请耐心等待审核') }}</p>
                </div>
                <div class="bottom">
                    <el-button class="btn" @click="jumpFunc">{{ $L('去逛逛') }}</el-button>

                </div>
            </div>
        </div>
        <div class="content-view"  v-if="type == 4">
            <div class="header">
                <div class="icon"><i class="el-icon-warning mainFontColor"></i></div>
                <div class="tips">
                    <p>{{ $L('分销功能已关闭，如有疑问请联系') }}</p>
                </div>
                <div class="bottom">
                    <el-button class="btn" @click="jumpFunc">{{ $L('去逛逛') }}</el-button>

                </div>
            </div>
        </div>
        <div class="content-view"  v-if="type == 5">
            <div class="header">
                <div class="icon"><i class="el-icon-warning mainFontColor"></i></div>
                <div class="tips">
                    <p>{{ $L('分销审核未通过') }}</p>
                </div>

                <div class="bottom">
<!--                    <el-button class="btn" type="primary">{{ $L('再次申请') }}</el-button>-->
                <el-button class="btn" @click="jumpFunc">{{ $L('去逛逛') }}</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import backToCart from "../../../components/distribution/backToCart.vue";
    export default {
        name: "distributorAudit",
        components: {backToCart},
        data () {
            return {
                type : 1,
                tableData: {},
                checkedProdList: [],
                prodArr:[],
                resultType:0
            }
        },
        created () {
            this.getData();
        },
        methods: {
            jumpFunc() {
                this.$router.push({name:'home'});
            },
            handleCheckAllChange(val){
            },
            goToBuy() {
                //让我们开始来处理数据吧!!!
                for (let i in this.checkedProdList) {
                    this.pidArr += "&pids["+i+"]="+this.checkedProdList[i].prodId;
                    this.idspecprice += "&IDSpecPrice["+this.checkedProdList[i].prodId+"]="+this.checkedProdList[i].specId;
                }

                this.$store.commit('setDialogObj', {
                    name: this.$L('购物车'),
                    view: backToCart,
                    width: '94%',
                    height: '100%',
                    data: {
                        pidArr:this.pidArr,
                        idspecprice:this.idspecprice,
                        ismobile:1
                    }
                });

            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('reviewResult', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.resultType = reData.data.resultType;
                        _self.tableData = reData.data;

                        if (_self.resultType == 4) {
                            _self.type = 4;
                        } else if (_self.resultType == 2) {
                            _self.type = 2;
                        } else if (_self.resultType == 1){
                            _self.$router.push({name:'distributorList'});
                        } else if (_self.resultType == 3){
                            _self.type = 3;
                        } else {
                            _self.type = 5;
                        }

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";

    .distributor-audit {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;

        .title {
            font-size: 24px;
        }
        .header {
            margin: 0 auto;
            padding: 20px 0;
            .icon {
                text-align: center;
                margin: 0 auto;

                i {
                    color: #fff;
                    line-height: 80px;
                    font-size: 50px;
                }
            }

            .tips {
                text-align: center;

                p {
                    padding: 0 10px 15px;

                    &:last-child {
                        font-size: 14px;
                    }
                }
                .title{
                    font-size: 14px;
                    color: #000000;
                    font-weight: 800;
                }
            }
        }

        .content-view {
            position: relative;
            margin: 10px 0;
            background: #fff;
            .content-item {
                width: 100%;
                border-top: 1px solid #ccc;
                padding: 20px;
                .tips{
                    padding-bottom: 20px;
                }
                .left{
                    height: 280px;
                    overflow-y: scroll;
                    .content-ul{
                        display: flex;
                        justify-content: flex-start;
                        box-sizing: border-box;
                        width: 100%;
                        text-align: left;
                        flex-wrap: wrap;
                        overflow: hidden;
                        .content-list{
                            width: 100%;
                            margin: 0 20px 20px 0;
                            .item{
                                .head{
                                    text-align: center;
                                    display: inline-block;
                                    .img{
                                        display: block;
                                        color: #666;
                                        width: 60px;
                                        height: 60px;
                                        display: inline-block;
                                        vertical-align: middle;
                                        &:hover{
                                            opacity: 0.8;
                                        }
                                    }
                                    .checkbox{
                                        display: inline-block;
                                    }
                                    &.left-item{
                                        width: 120px;
                                        vertical-align: top;
                                    }
                                }
                                .right-item{
                                    display: inline-block;
                                    width: calc(100% - 130px);
                                    padding-left: 10px;
                                    .name{
                                        text-align: left;
                                        padding: 10px 0;
                                        text-overflow: ellipsis;
                                        display: -webkit-box;
                                        -webkit-line-clamp: 2;
                                        -webkit-box-orient: vertical;
                                        overflow: hidden;
                                    }
                                    .details{
                                        text-align: left;
                                    }

                                }

                            }
                        }
                    }
                }

                .right{
                    text-align: center;
                    margin-top: 50px;
                    .btn{
                        width: 85px;
                    }
                }
            }

            .bottom {
                text-align: center;
                padding: 50px 0;
                .btn {
                    margin: 0 auto;
                    padding: 10px 30px;
                    display: inline-block;
                }
            }
        }
    }
</style>